<script>
    require('@/assets/pc/js/jquery-1.7.1.min.js');
    require('@/assets/pc/js/jquery.carousel.min.js');
    import {logout} from "@/api/user";

    export default {
        name: "Index",
        methods: {
            jump(index) {
                let i = parseInt(index);
                switch (i) {
                    case 1://
                        this.$router.push({name: ''});
                        break;
                    case 2://
                        this.$router.push({name: ''});
                        break;
                    case 3://
                        this.$router.push({name: ''});
                        break;
                    case 4://
                        this.$router.push({name: ''});
                        break;
                    case 5://
                        this.$router.push({name: ''});
                        break;
                    case 6://
                        this.$router.push({name: ''});
                        break;
                    case 7://
                        this.$router.push({name: ''});
                        break;
                    case 8://
                        logout()
                            .then(() => {
                                this.$info({
                                    title: '',
                                    content: (
                                        <div>
                                            <p></p>
                                        </div>
                                    ),
                                })
                                this.$router.push({name: 'login'});
                            })
                        break;
                    default:
                        break;
                }
            }
        },
        mounted() {
            $('#carousel').carousel({
                width: 880,
                height: 350,
                itemWidth: 150,
                itemHeight: 150,
                horizontalRadius: 300,
                verticalRadius: 100,
                resize: true,
                mouseScroll: true,
                mouseDrag: true,
                scaleRatio: 0.4,
                scrollbar: true,
                tooltip: true,
                mouseWheel: true,
                mouseWheelReverse: true
            });

            let _self = this;
            $(".carousel-item").click(function () {
                let src = $(this).attr('src');

                let index = src.split("guide_nav0")[1][0];
                // let index = src.substring(15, 16);
                _self.jump(index);
            });
        }
    }
</script>

